import * as React from "react";
import ReactRough, { Rectangle } from "react-rough";
import { StaticImage } from "gatsby-plugin-image";
import Button from "./button";
import { navigate } from "gatsby";
import portrait from "../images/portrait.png";

const Header = () => {
  let width = 630;
  const height = 180;

  const calcWidth = () => Math.min(width, document.documentElement.clientWidth);

  const [dimensions, setDimensions] = React.useState({
    height: 0,
    width: calcWidth(),
  });

  React.useEffect(() => {
    function handleResize() {
      console.log(dimensions.width);

      setDimensions({
        height: document.documentElement.clientHeight,
        width: calcWidth(),
      });
    }

    window.addEventListener("resize", handleResize);

    return () => {
      window.removeEventListener("resize", handleResize);
    };
  });

  const rough = (
    <div
      style={{
        marginTop: 30,
      }}
    >
      <div
        style={{
          position: "relative",
          width: dimensions.width,
          height,
          display: "inline-block",
          cursor: "pointer",
        }}
        onClick={() => {
          navigate("/");
        }}
      >
        <ReactRough height={height} width={dimensions.width} renderer="svg">
          {/* <Rectangle
        x={15}
        y={15}
        width={180}
        height={80}
        bowing={5}
        strokeWidth={4}
        fill={"#333"}
        fillWeight={4}
        hachureGap={6}
        fillStyle={"cross-hatch"}
      /> */}
          <Rectangle
            x={15}
            y={15}
            width={dimensions.width - 30}
            height={height - 30}
            bowing={2}
            fillStyle="solid"
            fill={"#aaa"}
            strokeWidth={4}
            roughness={2}
          />
        </ReactRough>
        {/* <StaticImage
          src="../images/portrait.png"
          width={200}
          height={200}
          quality={95}
          alt="Profile picture"
          placeholder="blurred"
          style={{
            position: "absolute",
            left: `30px`,
            bottom: "15px",
          }}
          loading="eager"
        /> */}
        <img
          src={portrait}
          style={{
            width: 200,
            height: 200,
            objectFit: "cover",
            position: "absolute",
            left: `30px`,
            bottom: "15px",
          }}
        />
        <div
          style={{
            position: "absolute",
            right: "40px",
            bottom: "20px",
            zIndex: 10,
            fontSize: 30,
            textAlign: "right",
            color: "#000",
          }}
        >
          James
          <br />
          <span style={{ color: "#c11111", fontWeight: 700 }}>Patrick</span>
          <br />
          LeDuc
        </div>
      </div>

      <Button text="Projects" link="projects" width={180} />
      <Button text="Bio" link="bio" />
      <Button text="Resume" link="resume" width={180} />
      <Button text="Log" link="log" />
    </div>
  );

  return <div>{rough}</div>;
};

export default Header;
